import { observable, runInAction } from 'mobx'
import auth from '../utils/auth'

export default new class User {
  @observable
  id = undefined

  @observable
  displayName = undefined

  @observable
  image = undefined

  constructor () {
    auth().then(({ id, displayName, image }) => {
      runInAction(() => {
        Object.assign(this, { id, displayName, image })
      })
    })
  }
}



